class Toast {
  constructor(params) {
    this._title =
      params["title"] === false ? false : params["title"] || "Title";
    // console.log(this._title);
    this._text = params["text"] || "Message...";
    this._theme = params["theme"] || "default";
    this._autohide = params["autohide"] && true;
    this._interval = +params["interval"] || 5000;
    this._ee = params.e;

    this.__INTERVAL = null;

    this._windowWidth = Math.ceil(window.innerWidth / 2);

    // console.log(this._title);
    this._create();
    this._show();
  }
  _show() {
    this._el.classList.add("toast_show");
    if (this._autohide) {
      this.__INTERVAL = setTimeout(() => {
        this._hide();
      }, this._interval);
    }
  }
  _hide() {
    clearInterval(this.__INTERVAL);

    this._el.classList.remove("toast_show");
    const event = new CustomEvent("hide.toast", {
      detail: { target: this._el },
    });
    document.dispatchEvent(event);
  }
  _create() {
    const el = document.createElement("div");
    el.classList.add("toast");
    el.classList.add(`toast_${this._theme}`);
    let html = `{header}<div class="toast__body"></div>`;
    const htmlHeader =
      this._title === false ? "" : '<div class="toast__header"></div>';
    html = html.replace("{header}", htmlHeader);
    el.innerHTML = html;
    if (this._title) {
      el.querySelector(".toast__header").textContent = this._title;
    } else {
      el.classList.add("toast_message");
    }
    el.querySelector(".toast__body").textContent = this._text;
    this._el = el;

    if (!document.querySelector(".toast-container")) {
      const container = document.createElement("div");

      console.log(this._windowWidth);

      container.classList.add("toast-container");

      document.body.append(container);
    }

    if (document.querySelector(".toast-container")) {
      const container = document.querySelector(".toast-container");

      console.log(this._windowWidth);
      console.log(this._ee.clientX);

      if (this._windowWidth < this._ee.clientX) {
        container.classList.remove("container-position-right");
        container.classList.add("container-position-left");
      } else if (this._windowWidth > this._ee.clientX) {
        container.classList.remove("container-position-left");
        container.classList.add("container-position-right");
      }
    }

    document.querySelector(".toast-container").append(this._el);
  }
}

export default Toast;
